<template>
  <div class="my" v-if="hasLogin">
   <!-- <div style="width: 100px;height:100px;background-color: #EA6F5A;" @click="checkTime">
    {{userInfo.user_lv}}
    </div> -->

  <div class="toptop">
    <!-- 头像 -->
    <UserTag :msgg="userInfo"></UserTag>
    <!-- <HeadPor :msgg="{ authen:userInfo.user_authen, img:userInfo.user_img,name:userInfo.user_name,user_lv:userInfo.user_lv,gold:userInfo.user_gold}"></HeadPor> -->
	<div class="folAndFans" >
		<div class="follow" @click="folAndFans('follow')">关注{{userInfo.user_follow}}</div>
		<div class="fans" @click="folAndFans('fans')">粉丝{{userInfo.user_fans}}</div>
	</div>

    <!-- 经验条 -->
    <div class="progress" >
      <!-- <div class="lvDiv">LV {{msgg.user_lv|fa}}</div> -->
      <!-- <div><i class="iconfont lv">&#xe60f;</i>{{userInfo.user_lv|fa}}</div> -->
      <div class="lvDiv">LV {{userInfo.user_lv| fLvComputed}}</div>
      <div>{{pivotText}}</div>
      <div class="lvDiv">LV {{userInfo.user_lv| fLvComputed| fLvComputedAdd}}</div>
      <!-- <div><i class="iconfont lv">&#xe60f;</i>{{userInfo.user_lv|fa|fb}}</div> -->
    </div>
    <!-- 经验进度条 -->
    <!-- <van-progress :percentage="percen"/> -->
    <!-- 签到按钮 -->
    <van-button type="primary" size="large" @click="checkTime" class='progressBottom' round >点击签到</van-button>

  </div>







      <div class="cellGroup">
        <!-- 举报 11111111111111111111111111111111111-->
        <div class="cell van-hairline--bottom" @click="toOther(4)" v-if="userInfo.user_super_admin == 1">
          <!-- 左边 -->
          <div class="left">
            <i class="iconfont mymyarticle">&#xe603;</i>
            <span>处理举报</span>
          </div>
          <!-- <i class="iconfont right">&#xe611;</i> -->
          <div class="rightTwo">
            <!-- <span>{{userInfo.user_article_count}}</span> -->
            <i class="iconfont right">&#xe611;</i>
          </div>
        </div>

        <!-- 我的收藏 11111111111111111111111111111111111111111-->
        <div class="cell van-hairline--bottom" @click="toOther(0)">
          <!-- 左边 -->
          <div class="left">
            <i class="iconfont mymyarticle">&#xe603;</i>
            <span>我的文章</span>
          </div>
          <!-- <i class="iconfont right">&#xe611;</i> -->
          <div class="rightTwo">
            <span>{{userInfo.user_article_count}}</span>
            <i class="iconfont right">&#xe611;</i>
          </div>
        </div>
        <!-- 我的收藏 1111111111111111111111111111111111111-->
        <div class="cell van-hairline--bottom" @click="toOther(1)">
          <!-- 左边 -->
          <div class="left">
            <i class="iconfont col">&#xe68b;</i>
            <span>我的收藏</span>
          </div>
          <div class="rightTwo">
            <span>{{userInfo.user_col_count}}</span>
            <i class="iconfont right">&#xe611;</i>
          </div>
        </div>
        <!-- 我的消息 111111111111111111111111111111111111-->
        <div class="cell van-hairline--bottom" @click="toInfo">
          <!-- 左边 -->
          <div class="left">
            <i class="iconfont col">&#xe68b;</i>
            <span>我的消息</span>
          </div>
          <div class="rightTwo">
            <span>{{userInfo.user_unread}}</span>
            <i class="iconfont right">&#xe611;</i>
          </div>
        </div>
        <!-- 创作 11111111111111111111111111111111111111-->
        <div class="cell van-hairline--bottom" @click="toEditor">
          <!-- 左边 -->
          <div class="left">
            <i class="iconfont mo">&#xe608;</i>
            <span>我要创作</span>
          </div>
          <!-- <i class="iconfont right">&#xe611;</i> -->
          <div class="rightTwo">
              <i class="iconfont right">&#xe611;</i>
           </div>
        </div>
        <!-- 修改资料 1111111111111111111111111111111111111111-->
        <div class="cell van-hairline--bottom" @click="toOther(2)">
          <!-- 左边 -->
          <div class="left">
            <i class="iconfont mo">&#xe608;</i>
            <span>修改资料</span>
          </div>
          <!-- <i class="iconfont right">&#xe611;</i> -->
          <div class="rightTwo">
              <i class="iconfont right">&#xe611;</i>
           </div>
        </div>
        <!-- 设置 111111111111111111111111111111111111111111111-->
        <div class="cell van-hairline--bottom" @click="toOther(3)">
          <!-- 左边 -->
          <div class="left">
            <i class="iconfont set">&#xe612;</i>
            <span>系统设置</span>
          </div>
          <!-- <i class="iconfont right">&#xe611;</i> -->
          <div class="rightTwo">
              <i class="iconfont right">&#xe611;</i>
           </div>
        </div>
      </div>

      <!-- <van-cell-group>
        <van-cell title="我的文章" is-link @click="toOther(0)"/>
        <van-cell title="我的收藏" is-link @click="toOther(1)"/>
        <van-cell title="修改资料" is-link @click="toOther(2)"/>
        <van-cell title="设置" is-link @click="toOther(3)"/>
      </van-cell-group> -->



  </div>
</template>

<script>
  /* 引入头像组件*/
  // import HeadPor from "@/components/utils/HeadPor"
  import UserTag from "@/components/utils/UserTag"
	/* 引入导航栏组件*/
	// import List from "@/components/utils/ListForMe"

  import {createNamespacedHelpers} from 'vuex'
  const login=createNamespacedHelpers('login')
  const list=createNamespacedHelpers('list')
  export default{
    name:"my",
    /* 数据*/
    data(){
      return {
        active: 0,
        // checkTime_waiting_response:false,
        ws_checkTime:false,
        percen : 0
        // 进度条文字
      }
    },
    filters:{
    },
    /* 方法*/
    methods:{
      folAndFans(params){
        this.$router.push({
          path:'/folAndFans',
          query : {
            folAndFans : params
          }
        })
      },
      toEditor(){
        this.$router.push({
          path : '/editor'
        })
      },
      toInfo(){
        this.$router.push({
          path : '/info'
        })
      },
      toOther(params){
        switch(params){
          case 0:
            this.$router.push({
              path:'/articleAndCol',
              query : {
                articleAndCol : 'article',
              // otherUserId : this.userInfo.user_id
              }
            })
             break;
          case 1:
            this.$router.push({
              path:'/articleAndCol',
              query : {
                articleAndCol : 'col',
              // otherUserId : this.userInfo.user_id
              }
            })
             break;
          case 2:
            this.$router.push({
              path:'/userData'
            })
             break;
          case 3:
            this.$router.push({
              path:'/setUp'
            })
             break;
          case 4:
            this.$router.push({
              path:'/report'
            })
             break;
        }

      },
      checkTime(){
        if(this.ws_checkTime == true){
          this.$toast("正在签到")
          return;
        }
        this.ws_checkTime = true
        this.$api.user.checkTime({}).then(
            res =>{
              this.ws_checkTime = false
                switch(res.data){
                  case 'repeat_check_in':
                      this.$dialog.alert({
                        message: '您今天已经签到了'
                      }).then(() => {
                        // on close
                      });
                      break;
                  default:
                  /* 连续签到次数*/
                  var timeLog = res.data.user_reTime_log,
                  /* 今天签到获得金币*/
                  getGold = ((timeLog+1)*10 +10),
                  /* 今天签到获得经验*/
                  getLv = ((timeLog+1)*100 +100);
                  //////////////////////////
                  this.userInfoHandler(res.data)
                  this.$dialog.alert({
                    title: '今日签到成功',
                    message: `恭喜您连续签到${timeLog}天,获得了${getGold}钻石和${getLv}点经验`
                  }).then(() => {
                    // on close
                  });
                }
            }
        )
      },

      // ...list.mapMutations({
      //       	oneHandler: 'oneHandler'
      //       }),
      // ...login.mapMutations({
      //         goldAndlvHandler: 'goldAndlvHandler'
      //       }),
      ...login.mapMutations({
              userInfoHandler: 'userInfoHandler'
            }),




    },
    /* 计算*/
    computed:{
    ...login.mapState({
         	hasLogin : state => state.hasLogin
         }),
    ...login.mapState({
         	userInfo : state => state.userInfo
         }),
      pivotText:{
         get () {
              var num1 = Math.floor(this.userInfo.user_lv/100)
              var num2 = (num1 + 1)*100
              return `${this.userInfo.user_lv}/${num2}`
            }
      },
    },
    // 组件
    components : {
    	/* 这个组件名字不能和原生标签名重复 */

      // HeadPor
      UserTag
    },
    /* 挂载阶段*/
    mounted() {
      /* 如果用户未登录则跳转到登录页*/
      if(!this.hasLogin){
        this.$router.replace({
          path:'/login'
        })
      }


      if(this.userInfo.user_lv){
          var num1 = Math.floor(this.userInfo.user_lv/100)
          var num2 = (num1 + 1)*100
          console.log(Math.floor(this.userInfo.user_lv/num2*100)+'123')
          this.percen = Math.floor(this.userInfo.user_lv/num2*100)
      }

    },
    activated(){

    },
    // 监听
    watch:{
    'userInfo.user_lv':{
	      handler(newVal, oldVal) {
	       var num1 = Math.floor(this.userInfo.user_lv/100)
         var num2 = (num1 + 1)*100
         console.log(Math.floor(this.userInfo.user_lv/num2*100)+'123')
         this.percen = Math.floor(this.userInfo.user_lv/num2*100)
	      },
	      // immediate: true,
	      // deep: true
	  },
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../assets/scss/me/my.scss";
</style>



